import { h } from 'preact'
import PropTypes from 'prop-types'

const Bar = ({ percentage, barClass, barColor }) => (
  <div className="flex bar">
    <div
      className={barClass}
      style={{
        width: `${percentage}%`,
        backgroundColor: barColor,
      }}
    />
  </div>
)

Bar.propTypes = {
  percentage: PropTypes.number,
}

export default Bar
