import { h } from 'preact'
import RatingsHeader from './components/ratingsHeader'
import TopCoinCard from './components/topCoinCard'
import TableRow from './components/tableRow'
import TableHeader from './components/tableHeader'
import RatingsFooter from './components/ratingsFooter'

import './ratings-page.css'

import Bar from './components/bar'

const RatingsPage = ({
  topCoins,
  valuation,
  colors,
  bitcoinPredict,
  restCoins,
  confidence,
  date,
}) => (
  <div className="content-wrapper">
    <RatingsHeader confidence={confidence} date={date} />
    <div className="cards-section">
      {topCoins.map((coin) => <TopCoinCard {...coin} />)}
    </div>
    <div className="table-section">
      <TableHeader />
      {restCoins.map((coin, index) => (
        <TableRow {...coin} isEven={index % 2 === 0} />
      ))}
    </div>
    <div className="flex predict-section">
      <div className="rounded predict-card">
        <div className="header">
          <span>“In one year from now, the Crypto market will go…”</span>
        </div>
        <div className="content">
          {valuation.map((item, index) => (
            <div className="flex line">
              <span>{item.label}</span>
              <div className="line-bar">
                <Bar percentage={item.value} barColor={colors[index]} />
                <span className="digit">{item.value} %</span>
              </div>
            </div>
          ))}
        </div>
      </div>
      <div className="rounded predict-card right">
        <div className="header">
          <span>Bitcoin Will Be Worth...</span>
          <span className="column-title">IN 1 YEAR</span>
          <span className="column-title">IN 5 YEARS</span>
        </div>
        <div className="content">
          {bitcoinPredict.map((item, index) => (
            <div className="flex line">
              <span>{item.label}</span>
              <div className="line-bar">
                <Bar percentage={item.value1} barColor={colors[index]} />
                <span className="digit">{item.value1} %</span>
              </div>
              <div className="line-bar">
                <Bar percentage={item.value2} barColor={colors[index]} />
                <span className="digit">{item.value2} %</span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
    <RatingsFooter />
  </div>
)

export default RatingsPage
